import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter);

const router = new VueRouter({
    mode: 'history',
    routes: [
        {
            path: '/',
            name: 'main',
            component: () => import(/* webpackChunkName: "main" */"../views/Main")
        },
        {
            path: '/functional-component',
            name: 'functional-component',
            component: () => import(
                /* webpackChunkName: "bench-functional-component" */
                "../views/Functional"
            )
        },
        {
            path: '/conditional-render',
            name: 'conditional-render',
            component: () => import(
                /* webpackChunkName: "bench-conditional-render" */
                "../views/Conditional"
            )
        },
        {
            path: '/keep-alive',
            component: () => import(
                /* webpackChunkName: "bench-keep-alive" */
                "../views/Keep"
            ),
            children: [
                {
                    path: '',
                    name: 'keep-alive-prev',
                    component: () => import(
                        /* webpackChunkName: "bench-keep-alive-off" */
                        "../components/Benchmarks/KeepAlive/KeepAliveOff"
                    ),
                },
                {
                    path: 'next',
                    name: 'keep-alive-next',
                    component: () => import(
                        /* webpackChunkName: "bench-keep-alive-on" */
                        "../components/Benchmarks/KeepAlive/KeepAliveOn"
                    ),
                },
            ],
        },
        {
            path: '/child',
            name: 'child',
            component: () => import(
                /* webpackChunkName: "bench-child" */
                "../views/Child"
            )
        },
        {
            path: '/local-var',
            name: 'local-var',
            component: () => import(
                /* webpackChunkName: "bench-local" */
                "../views/LocalVar"
            )
        },
        {
            path: '/deferred',
            name: 'deferred',
            component: () => import(
                /* webpackChunkName: "bench-deferred" */
                "../views/Deferred"
            )
        },
    ],
});

export default router
